<template>
  <main>
    <div v-if="finish" class="box-quiz">
      <div class="question">
        <div>  {{getCurrentQuestion.question}}</div>
        <div class="score">{{score}}/{{lengthQuestion}}</div>
      </div>
      <div class="options" v-for="(option,index) in getCurrentQuestion.options" 
      
      >
        <label :class="`option ${getCurrentQuestion.selected ==index?
        getCurrentQuestion.selected == getCurrentQuestion.answer? 'correct':'faild' :''}` ">
          <input 
          type="radio" 
						:value="index" 
						v-model="getCurrentQuestion.selected" 
						:disabled="getCurrentQuestion.selected"
						@change="SetAnswer" 
          >
         <span>{{option}} </span> 
        </label> 

      </div>
      <button
       :class="`button ${currentQestion == lengthQuestion - 1 && getCurrentQuestion.selected != null ? 'end': ''}`"
       @click="getCurrentQuestion.selected != null? nextQuestion() :''"
       
       >
      {{
        currentQestion < lengthQuestion - 1?
      getCurrentQuestion.selected == null?'choose the answer': 'next question'
      :getCurrentQuestion.selected == null?'choose the answer': 'finish'
      }} 
      </button>
      
    </div>
    <div  v-else class="finish">
      <p> The Quiz Is Finished  and you get ({{score}}/{{lengthQuestion}}) Score</p>

    </div>
  </main>
</template>

<script setup>
  import { ref, computed } from 'vue';
  import questions from '../questions.json';
  let currentQestion=ref(0);
  const finish=ref(true)
  const question=ref(questions.questions);
  const lengthQuestion=ref(question.value.length)
  const score=computed(()=>{
    let value=0;
   
    question.value.map(q=>q.selected != null ? q.selected == q.answer? value++: value:'')
    if(getCurrentQuestion.selected !=null && getCurrentQuestion.selected == getCurrentQuestion.answer){
      value++
    }
    return value
  });

  const getCurrentQuestion=computed(()=>{
    let currentQuestion=question.value[currentQestion.value];
    return currentQuestion;
  });

const SetAnswer = (e) => {
	question.value[currentQestion.value].selected = e.target.value
	e.target.value = null
}
let nextQuestion=function(){
  
  if(currentQestion.value< question.value.length -1){
    currentQestion.value++
  }
  else{
    finish.value=false
  }
  console.log(currentQestion.value)
}
</script>


<style >
