  margin: auto;
  width: 600px;
  min-height: 300px;
  background-color: rgb(67, 67, 113);
  position:absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  font-size: larger;
  padding: 20px;
  box-shadow:  rgb(17, 27, 117) 2px 2px 10px, rgb(17, 27, 117) -2px -2px 10px;
  
}
.question{
  display: flex;
  justify-content: space-between;
  border-bottom: #FFF solid 1px;
 
}
.options{
  background-color: rgb(18, 105, 156);
  text-align: start;
  margin: 10px 0px;
 
}
.options:hover{
  background-color: rgb(43, 23, 198);
}
.option span{
  flex: 1;
}
.option{
  cursor: pointer;
 display: flex;
 padding: 10px;
}
.option input{
  display: none;
}
.correct{
  background-color: green;
}
.faild{
  background-color: red;
}
.finish{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  font-size: xx-large;
  color: #FFF
}
